import React from 'react';
import "./drPredCmp.css"

function TodoApp({task, tasks, inputTask, addTask, resetTask}) {
    return (
        <div>
            <input type="text" onChange={(e) => inputTask(e.target.value)} />
            <input type="button" value="add" onClick={() => addTask(task)} />
            <input type="button" value="reset" onClick={() => resetTask()} />
            <ul>
                {
                    tasks.map((item, i) =>
                            <li key={i}>{item}</li>
                    )
                }
            </ul>
        </div>
    );
}

// React Component
//const drPredCmp = (header, features, predicts, doPost) => {
const drPredCmp = (props) => {
    console.log(props.header, props.features, props.predicts);
    return (
        <div>
            <div>
                <table border="1">
                    <thead>
                        <tr>
                            {props.header.map((hds,index) =>
                                <th key={index}>{hds}</th>)}
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            {props.features.map((fts,index) =>
                                <td key={index}>{fts}</td>)}
                        </tr>
                    </tbody>
                </table>
            </div>
            <input type="button" value="予測！" onClick={() => props.doPost(props.header, props.features)} />
           <div>
                予測結果：
                <ul>
                { props.predicts.map((prds,index) =>
                    <li key={index}>{prds.predictionValues['0'].value}</li>
                    ) }
                </ul>
            </div>
        </div>
    );

};

export default drPredCmp;
