import {POST_REQUEST, POST_SUCCESS, POST_FAILURE} from '../actions/drAction';

const initialState = {
    header: ['ID','メンバーID','ローン額','ローン目的タイプ','ローン目的','肩書','勤続年数','家所有','年収入','郵便番号','住所（州）','ローン対収入','グレード','サブグレード','滞納（過去二年）','クレジットライン開始','照会数（過去6ヶ月）','直近の滞納からの月数','採集記録からの月数','開いているアカウント数','悪評記録数','リボ払い残高','リボ枠活用率','過去の全アカウント数','ローンリストステータス初期値','徴収数（過去12ヶ月）','前回の悪評価からの月数','申し込みタイプ','現行滞納アカウント数','徴収総額','全口座残高'],
    features: [3296446,4068857,11200,'credit_card','Credit Card','Nokia Siemens Network',10,'OWN',108000,'750xx','TX',12.52,'A','A2',0,'Oct-02',0,,,9,0,37822,0.662,21,'f',0,,'INDIVIDUAL',0,0,187717],
    predicts: []
}

function drReducer (state = initialState, action) {
    switch (action.type) {
        case POST_REQUEST:
            return {
                ...state,
                isFetching: true,
                predicts: []
            };
        case POST_SUCCESS:
            return {
                ...state,
                isFetching: false,
                predicts: action.predict['data']
            };
        case POST_FAILURE:
            return {
                ...state,
                isFetching: false,
                error: action.error
            };
        default:
            return state;
    }
};

export default drReducer;