import axios from 'axios'

// axios インスタンスを生成する (DRアクセス用設定)
const axios_inst = axios.create({
    baseURL: 'https://deploytraining2.orm.datarobot.com/',
    headers: {
      'Content-Type': 'text/plain; charset=UTF-8',
      'datarobot-key': '48893893-f796-8395-bec8-5d1f10f4c307'
    },
    auth: {
        username: 'n-okabe+dru@nri.co.jp',
        password: 'oxw__eHEdRmKMPpCLkzfcvKo1-HZUsIY'
    }
  });
  

export const POST_REQUEST = 'POST_REQUEST'
const postRequest = () => {
  return {
    type: POST_REQUEST
  }
}

export const POST_SUCCESS = 'POST_SUCCESS'
const postSuccess = (json) => {  
  return {
    type: POST_SUCCESS,
    predict: json,
  }
}

export const POST_FAILURE = 'POST_FAILURE'
const postFailure = (error) => {
  return {
    type: POST_FAILURE,
    error
  }
}

export const doPost = (header, features) => {
    return (dispatch) => {
        dispatch(postRequest)

        // DR API Call
        return axios_inst.post('/predApi/v1.0/deployments/5d0088ff87cf0a137f663382/predictions',
             [...header] + '\n' + [...features]
        ).then(res =>
            dispatch(postSuccess(res.data))
        ).catch(err => 
            dispatch(postFailure(err))
        )
    }
}
