import { connect } from  'react-redux';
import drPredCmp from '../components/drPredCmp';
import {POST_FAILURE,POST_REQUEST,POST_SUCCESS,doPost} from '../actions/drAction';

function mapStateToProps({ header, features, predicts }) {
    console.log(header, features, predicts);
    return {
        header,
        features,
        predicts
    };
}

function mapDispatchToProps(dispatch) {
    return {
        doPost(header, features) {
            dispatch(doPost(header, features));
        }
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(drPredCmp);
